.swiper-slide {
  max-width: 16rem;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  align-items: flex-start;

  & h3 {
    font-family: "Bebas Neue";
    font-size: 1.2rem;
    color: #3877ee;
    line-height: 120%;
    font-weight: 400;
  }

  & p {
    color: #42567a;
    font-weight: 700;
    font-size: 1rem;
    line-height: 120%;
  }

  @media (max-width: 430px) {
    max-width: 14rem;
    & h3 {
      font-size: 2rem;
    }
    & p {
      font-weight: 400;
      font-size: 1.2rem;
      line-height: 140%;
    }
  }
}
